import dayjs from "dayjs";
import type { financeItem } from "../Data/getFinance";

export const geenrateSubtext1 = (key: string, ts_code: string) => {
  let title = "";
  const finance: financeItem[] = window.financeData[ts_code] || [];

  const len = finance?.length;
  if (len) {
    const lastItem: any = finance[len - 1];
    const value = lastItem[key];
    const end_date = lastItem.end_date;
    title = `${dayjs(end_date).format("YYYY-MM-DD")}: ${value.toFixed(1)}`;

    const preEnd_date = dayjs(end_date).subtract(1, "year").format("YYYYMMDD");
    const preItem: any = finance.find((item: financeItem) => {
      return item.end_date === preEnd_date;
    });
    const preValue = preItem[key];
    const tb = (((value - preValue) / preValue) * 100).toFixed(2);
    title = `${title}, : ${tb}%`;
  }

  return title;
};

export const geenrateSubtext2 = (
  finance: financeItem[],
  key1: string,
  key2: string
) => {
  let title = "";

  const item: any = finance[finance?.length - 1];
  if (item) {
    const value = item[key1];
    const end_date = item.end_date;
    title = `${dayjs(end_date).format("YYYY-MM-DD")}: ${value.toFixed(1)}`;

    title = `${title}, : ${item[key2]?.toFixed(2)}%`;
  }

  return title;
};

export const tooltipPosition = (
  pos: any,
  params: any,
  el: any,
  elRect: any,
  size: any
) => {
  var obj: any = { top: 10 };
  obj[["left", "right"][+(pos[0] < size.viewSize[0] / 2)]] = 30;
  return obj;
};
