import { Fragment, useEffect, useState } from "react";
import classnames from "classnames";
import { Button, Divider, Space } from "antd";
import styles from "./index.module.css";
import dayjs from "dayjs";
import ModuleDetail from "../ModuleDetail";

const filterIndustry = (list: any) => {
  return list.filter((item: any) => {
    const { list_date, name } = item;

    const is1 = !name.includes("ST");
    const is2 = dayjs().year() - dayjs(list_date).year() >= 4;

    return is1 && is2;
  });
};
const listToTree = (list: Record<string, any>[], level: number) => {
  const tree: any = {};
  list.forEach((item: any) => {
    const { l1_name, l2_name, l3_name } = item;
    if (!tree[l1_name]) {
      tree[l1_name] = {};
    }
    if (level >= 2) {
      if (!tree[l1_name][l2_name]) {
        tree[l1_name][l2_name] = {};
      }
    }
    if (level >= 3) {
      if (!tree[l1_name][l2_name][l3_name]) {
        tree[l1_name][l2_name][l3_name] = [];
      }
    }
    if (level >= 4) {
      tree[l1_name][l2_name][l3_name].push(item);
    }
  });
  return tree;
};
const sortList = (list: any) => {
  list.sort((a: any, b: any) => {
    return (b.roic ?? -999) - (a.roic ?? -999);
  });

  return list;
};

const PageIndustry = (props: any) => {
  const { self, setSelf } = props;
  const industry = window.industry;

  const [currentTsCode, setCurrentTsCode] = useState<any>(null);

  const [tree, setTree] = useState<any>({});
  const [count, setCount] = useState(0);

  const generateData = async () => {
    let d = filterIndustry(industry);
    setCount(d.length);

    d = listToTree(d, 4);
    setTree(d);
  };

  const openGoogle = (text: string) => {
    window.open(`https://www.google.com/search?q=${text}`, "_blank");
  };

  useEffect(() => {
    if (industry.length) {
      generateData();
    }
  }, [industry, self]);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.leftContent}>
          <div className={classnames(styles.wrapIndustry)}>
            {Object.keys(tree).map((k1: string) => {
              if (!tree[k1]) return null;

              return (
                <Fragment key={k1}>
                  <br />
                  <div className="anchor" id={k1} data-name={k1}></div>
                  <Divider orientation="left" style={{ color: "rgb(255 0 0)" }}>
                    <span
                      style={{ cursor: "pointer" }}
                      onClick={() => openGoogle(k1)}
                    >
                      {k1}
                    </span>
                  </Divider>
                  {Object.keys(tree[k1]).map((k2) => {
                    return (
                      <Fragment key={k2}>
                        <div
                          className={styles.paddingRight20}
                          onClick={() => openGoogle(k2)}
                        >
                          {k2}
                        </div>
                        {Object.keys(tree[k1][k2]).map((k3) => {
                          const k3List = sortList(tree[k1][k2][k3]);

                          return (
                            <Fragment key={k3}>
                              <div
                                className={styles.paddingRight40}
                                onClick={() => openGoogle(k3)}
                              >
                                {k3}
                              </div>
                              <div className={styles.paddingRight60}>
                                {k3List.map((item: any, index: number) => {
                                  const { name, ts_code, roic } = item;
                                  const { isBuy, tag } = self?.[ts_code] || {};
                                  let tagStyle: any = {};
                                  if (isBuy) {
                                    tagStyle = {
                                      color: "red",
                                      border: "1px solid red",
                                    };
                                  } else if (tag) {
                                    tagStyle = {
                                      border: "1px solid red",
                                    };
                                  }

                                  return (
                                    <span
                                      key={`${ts_code}_${index}`}
                                      className={classnames(styles.tagStyle)}
                                      style={tagStyle}
                                      onClick={() => {
                                        setCurrentTsCode(ts_code);
                                      }}
                                    >
                                      <span>{name.slice(0, 4)}</span>
                                      <span>
                                        {roic ? `(${roic.toFixed(1)})` : ""}
                                      </span>
                                    </span>
                                  );
                                })}
                              </div>
                            </Fragment>
                          );
                        })}
                      </Fragment>
                    );
                  })}
                </Fragment>
              );
            })}
          </div>
        </div>
        <div className={styles.rightNav}>
          <Space size="small" direction="vertical">
            <div>Count: {count}</div>
            <Button block type="dashed" onClick={() => props.setRouter("home")}>
              
            </Button>
            {Object.keys(tree)?.map((name: string) => {
              return (
                <Button
                  style={{ color: "#2a5ea7" }}
                  key={name}
                  size="small"
                  type="link"
                  href={`#${name}`}
                >
                  {name}
                </Button>
              );
            })}
          </Space>
        </div>
      </div>

      <ModuleDetail
        ts_code={currentTsCode}
        self={self}
        setSelf={setSelf}
        onCancel={() => setCurrentTsCode(null)}
      />
    </>
  );
};

export default PageIndustry;
