import dayjs from "dayjs";
import { financeItem } from "../../Data/getFinance";
import { geenrateSubtext1 } from "../../utils/generateEChartOption";
import EChartReact from "../EChartReact";

const Efficiency = (props: any) => {
  const { finance, ts_code } = props;

  const getDebtOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("debt_to_assets", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.debt_to_assets),
          type: "bar",
          showBackground: true,
          animation: false,
        },
        {
          data: finance?.map(() => ({ value: 50, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const getCurrentRatio = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("current_ratio", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.current_ratio),
          type: "bar",
          showBackground: true,
          animation: false,
        },
        {
          data: finance?.map(() => ({ value: 1.5, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const getQuickRatio = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("quick_ratio", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.quick_ratio),
          type: "bar",
          showBackground: true,
          animation: false,
        },
        {
          data: finance?.map(() => ({ value: 1, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const debtOption = getDebtOption();
  const currentRatioOption = getCurrentRatio();
  const quickRatioOption = getQuickRatio();

  return (
    <div style={{ display: "flex", width: "100%" }}>
      <EChartReact option={debtOption} />
      <EChartReact option={currentRatioOption} />
      <EChartReact option={quickRatioOption} />
    </div>
  );
};
export default Efficiency;
