import dayjs from "dayjs";
import { financeItem } from "../../Data/getFinance";
import {
  geenrateSubtext1,
  geenrateSubtext2,
} from "../../utils/generateEChartOption";
import EChartReact from "../EChartReact";

const ARoicEps = (props: any) => {
  const { finance, ts_code } = props;

  const getRoicOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "ROIC",
        subtext: geenrateSubtext1("roic", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.roic),
          type: "bar",
          animation: false,
          showBackground: true,
        },
        {
          data: finance?.map(() => ({ value: 10, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const getEpsOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "EPS",
        subtext: geenrateSubtext2(finance, "eps", "basic_eps_yoy"),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.eps),
          type: "bar",
          showBackground: true,
          animation: false,
        },
      ],
    };
    return option;
  };

  const roicOption = getRoicOption();
  const epsOption = getEpsOption();

  return (
    <div style={{ display: "flex", width: "100%" }}>
      <EChartReact option={roicOption} />
      <EChartReact option={epsOption} />
    </div>
  );
};
export default ARoicEps;
