import { useEffect, useState } from "react";
import { selfCodeItem } from "../../Data/useSelf";
import styles from "./index.module.css";
import { Button, Divider, Input } from "antd";
import ItemK from "../ItemK";
import ModuleDetail from "../ModuleDetail";

const PageSelf = (props: any) => {
  const { self, setSelf } = props;

  const [currentTsCode, setCurrentTsCode] = useState<any>(null);

  const [selfList, setSelfList] = useState<selfCodeItem[]>([]);
  const [keyword, setKeyword] = useState("");

  useEffect(() => {
    if (self) {
      const list: selfCodeItem[] = [];
      Object.keys(self)?.forEach((ts_code: string) => {
        list.push(self[ts_code]);
      });
      list.sort((a: selfCodeItem, b: selfCodeItem) => {
        return (b.isBuy ? 1 : 0) - (a.isBuy ? 1 : 0);
      });
      setSelfList(list);
    }
  }, [self]);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.left}>
          <Button block type="dashed" onClick={() => props.setRouter("home")}>
            
          </Button>
          <br />
          <Input
            size="small"
            allowClear
            value={keyword}
            onChange={(e: any) => setKeyword(e.target.value)}
          />
          <div className={styles.list}>
            {selfList
              ?.filter((item: selfCodeItem) => {
                if (keyword) {
                  return item?.name?.includes(keyword);
                }
                return true;
              })
              ?.map((item: selfCodeItem) => {
                const { name, isBuy } = item;
                return (
                  <Button
                    style={{ textDecoration: "none" }}
                    key={name}
                    type="link"
                    href={`#${name}`}
                  >
                    <span
                      style={{ color: isBuy ? "rgba(255, 0, 0, 0.7)" : "" }}
                    >
                      {name}
                    </span>
                  </Button>
                );
              })}
          </div>
        </div>
        <div className={styles.content}>
          {selfList?.map((item: selfCodeItem) => {
            const { name, ts_code, isBuy } = item;

            return (
              <div id={name} key={name}>
                <Divider orientation="left">
                  <span
                    style={{
                      color: isBuy ? "rgba(255, 0, 0, 0.7)" : "",
                      cursor: "pointer",
                    }}
                    onClick={() => setCurrentTsCode(ts_code)}
                  >
                    {name}
                  </span>
                </Divider>
                <ItemK ts_code={ts_code} />
              </div>
            );
          })}
        </div>
      </div>
      <ModuleDetail
        ts_code={currentTsCode}
        self={self}
        setSelf={setSelf}
        onCancel={() => setCurrentTsCode(null)}
      />
    </>
  );
};

export default PageSelf;
