import { useEffect, useState } from "react";
import qs from "qs";
import "./App.css";
import PageHome from "./Components/PageHome";
import PageSelf from "./Components/PageSelf";
import PageManage from "./Components/PageManage";
import PageIndustry from "./Components/PageIndustry";
import initDta from "./Data/initDta";
import useSelf from "./Data/useSelf";
import { Spin } from "antd";

const getQueryObj = () => {
  return qs.parse(window.location.search, {
    ignoreQueryPrefix: true,
  });
};

function App() {
  const { self, setSelf } = useSelf();
  const [loading, setLoading] = useState(true);

  const [router, setRouter] = useState(getQueryObj().router);

  const setRouterFunc = (name: string, ts_codeString?: string) => {
    const queryString = qs.stringify({
      router: name,
      code: ts_codeString,
    });
    window.history.pushState(
      {},
      document.title,
      window.location.pathname + `?${queryString}`
    );
    setRouter(name);
  };

  useEffect(() => {
    initDta().then(() => {
      setLoading(false);
    });

    const goto = () => {
      setRouter(getQueryObj().router);
    };
    window.addEventListener("popstate", goto);
    return () => {
      window.removeEventListener("popstate", goto);
    };
  }, []);

  if (loading) {
    return (
      <Spin
        spinning={loading}
        size="large"
        style={{
          width: "100vw",
          height: "100vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      ></Spin>
    );
  }

  return (
    <>
      {!router || router === "home" ? (
        <PageHome
          loading={loading}
          setRouter={(name: string, ts_codeString: string) =>
            setRouterFunc(name, ts_codeString)
          }
        />
      ) : null}
      {router === "self" ? (
        <PageSelf
          router={router}
          self={self}
          setSelf={setSelf}
          setRouter={(name: string, ts_codeString: string) =>
            setRouterFunc(name, ts_codeString)
          }
        />
      ) : null}
      {router === "industry" ? (
        <PageIndustry
          router={router}
          self={self}
          setSelf={setSelf}
          setRouter={(name: string, ts_codeString: string) =>
            setRouterFunc(name, ts_codeString)
          }
        />
      ) : null}
      {router === "manage" ? (
        <PageManage
          setRouter={(name: string, ts_codeString: string) =>
            setRouterFunc(name, ts_codeString)
          }
        />
      ) : null}
    </>
  );
}

export default App;
