import { Button, Descriptions, Space } from "antd";
import dayjs from "dayjs";

import styles from "./index.module.css";

const exchangeMap: any = {
  SSE: "",
  SZSE: "",
  BSE: "",
};

const ItemStockInfo = (props: any) => {
  const { data } = props;

  if (!data.ts_code) {
    return null;
  }

  let {
    ts_code,
    main_business,
    name,
    list_date,
    website,
    employees,
    act_name,
    act_ent_type,
    exchange,
    market,
    l1_name,
    l2_name,
    l3_name,
  } = data;
  const code = ts_code.split(".").reverse().join("");

  if (!website.startsWith("http")) {
    website = "http://" + website;
  }

  const items: any = [
    {
      key: "name",
      label: "",
      children: <div>{name}</div>,
    },
    {
      key: "list_date",
      label: "",
      children: (
        <div>
          {dayjs(list_date).format("YYYY-MM-DD")}&nbsp;&nbsp;(
          {dayjs().year() - dayjs(list_date).year()})
        </div>
      ),
    },
    {
      key: "exchange",
      label: "",
      children: <div>{exchangeMap[exchange] + "-" + market}</div>,
    },
    {
      key: "employees",
      label: "",
      children: <div>{employees}</div>,
    },
    {
      key: "website",
      label: "",
      children: (
        <a href={website} target="_blank" rel="noreferrer">
          {ts_code}
        </a>
      ),
    },
    {
      key: "l1",
      label: "",
      children: <div>{`${l1_name}-${l2_name}-${l3_name}`}</div>,
    },
    {
      key: "act_name",
      label: "",
      span: 2,
      children: (
        <div>
          {act_name}
          {act_ent_type ? `--(${act_ent_type})` : undefined}
        </div>
      ),
    },
    {
      key: "main_business",
      span: 4,
      label: "",
      children: <div className={styles.main_business}>{main_business}</div>,
    },
    {
      key: "sites",
      label: "",
      span: 4,
      children: (
        <Space>
          <Button
            type="link"
            target="_blank"
            href={`https://wglh.com/stock/s/${code}`}
          >
            
          </Button>
          <Button
            type="link"
            target="_blank"
            href={`https://emweb.securities.eastmoney.com/pc_hsf10/pages/index.html?type=web&code=${code}&color=b#/gdyj`}
          >
            
          </Button>
          <Button
            type="link"
            target="_blank"
            href={`https://www.iwencai.com/unifiedwap/result?w=${name}&querytype=stock&issugs&sign=1732338954815`}
          >
            
          </Button>
          <Button
            type="link"
            target="_blank"
            href={`https://xueqiu.com/S/${code}`}
          >
            
          </Button>
        </Space>
      ),
    },
  ];

  return (
    <div>
      <Descriptions
        size="small"
        bordered
        column={4}
        title={null}
        items={items}
        labelStyle={{ width: 130 }}
      />
    </div>
  );
};

export default ItemStockInfo;
