import { Checkbox, Select, Spin } from "antd";
import { useEffect, useState } from "react";
import { selfCodeItem } from "../../Data/useSelf";

const options = [
  { label: 1, value: 1 },
  { label: 2, value: 2 },
  { label: 3, value: 3 },
  { label: 4, value: 4 },
  { label: 5, value: 5 },
  { label: 6, value: 6 },
  { label: 7, value: 7 },
  { label: 8, value: 8 },
  { label: 9, value: 9 },
];

const BaseMark = (props: any) => {
  const { ts_code, self, setSelf } = props;
  const industry = window.industry;

  const [spinning, setSpinning] = useState(false);
  const [isBuy, setIsBuy] = useState(false);
  const [tag, setTag] = useState();

  useEffect(() => {
    if (ts_code) {
      const { isBuy: _isBuy, tag: _tag } = self?.[ts_code] || {};
      setIsBuy(_isBuy);
      setTag(_tag);
    }
  }, [ts_code, self]);

  const getStockName = () => {
    const item = industry?.find((value: any) => {
      return value?.ts_code === ts_code;
    });
    return item?.name;
  };

  const handleSelfChange = (e: any) => {
    const ck = e.target.checked;
    modify({
      ts_code,
      name: getStockName(),
      isBuy: ck,
      tag,
    });
  };

  const handleTagChange = (value: number) => {
    modify({
      ts_code,
      name: getStockName(),
      isBuy,
      tag: value,
    });
  };

  const modify = async (newSelf: selfCodeItem) => {
    setSpinning(true);
    if (newSelf?.name) {
      await setSelf(newSelf);
    }
    setSpinning(false);
  };

  return (
    <div>
      <Spin spinning={spinning}>
        <Checkbox checked={isBuy} onChange={handleSelfChange}></Checkbox>
        &nbsp;&nbsp;
        <Select
          allowClear
          value={tag}
          placeholder="-"
          onChange={handleTagChange}
          style={{ width: "50px" }}
          listHeight={300}
          options={options}
        ></Select>
      </Spin>
    </div>
  );
};

export default BaseMark;
