import { useEffect, useState } from "react";
import dayjs from "dayjs";
import { Modal } from "antd";

import BaseMark from "../BaseMark";
import ItemK from "../ItemK";
import ItemStockInfo from "../ItemStockInfo";
import styles from "./index.module.css";
import { financeItem } from "../../Data/getFinance";

import ARoicEps from "./ARoicEps";
import BProfit from "./BProfit";
import CTurn from "./CTurn";
import DDebt from "./DDebt";

const ModuleDetail = (props: any) => {
  const { ts_code, setSelf, self } = props;
  const industry = window.industry;

  const [baseInfo, setBaseInfo] = useState<any>({});
  const [finance, setFinance] = useState<financeItem[] | null>(null);

  const init = async () => {
    const b = industry?.find((item: any) => {
      return item.ts_code === ts_code;
    });
    setBaseInfo(b);

    const f = window.financeData[ts_code];
    const finance = f?.filter((item: financeItem, index: number) => {
      const { end_date } = item;

      if (index === f?.length - 1) {
        return true;
      } else if (dayjs(end_date).month() === 11) {
        return true;
      }
      return false;
    });
    setFinance(finance);
  };

  useEffect(() => {
    if (ts_code) {
      init();
    }
  }, [ts_code]);

  return (
    <Modal
      width="97vw"
      centered
      destroyOnClose
      title={null}
      footer={null}
      open={ts_code}
      closable={false}
      onCancel={() => props?.onCancel(null)}
    >
      <div className={styles.container}>
        <div className={styles.left}>
          <ItemStockInfo data={baseInfo} />
          <br />
          <ARoicEps finance={finance} ts_code={ts_code} />
          {/*  */}
          <BProfit finance={finance} ts_code={ts_code} />
          {/*  */}
          <CTurn finance={finance} ts_code={ts_code} />
          {/*  */}
          <DDebt finance={finance} ts_code={ts_code} />
          {/*  -  */}
          {/* / */}
          {/*  */}
          {/*  */}
          {/*  +  */}
          {/*  +  */}

          <ItemK ts_code={ts_code} />
        </div>
        <div className={styles.right}>
          <BaseMark ts_code={ts_code} self={self} setSelf={setSelf} />
        </div>
      </div>
    </Modal>
  );
};

export default ModuleDetail;
