import localforage from "localforage";
import { trans1 } from "../utils/trans";
import { CommonApiResponse } from "../Components/PageManage";
import getFinance, { financeItem } from "./getFinance";
import dayjs from "dayjs";

const currentMonth = dayjs().month() + 1;
const currentYear = dayjs().year();
const useDate = (() => {
  if ([11, 12, 1, 2, 3, 4].includes(currentMonth)) {
    return `${currentYear}0930`;
  } else {
    return `${currentYear - 1}1231`;
  }
})();

const mergeData = async (
  financeData: Record<string, financeItem[]>
): Promise<Record<string, any>[]> => {
  const industryOrigin = (await localforage.getItem(
    "industry"
  )) as CommonApiResponse;
  const stock_basicOrigin = (await localforage.getItem(
    "stock_basic"
  )) as CommonApiResponse;
  const stock_companyOrigin = (await localforage.getItem(
    "stock_company"
  )) as CommonApiResponse;

  let industry = trans1(industryOrigin, [
    "l1_name",
    "l2_name",
    "l3_name",
    "ts_code",
  ]);
  const basic = trans1(stock_basicOrigin, [
    "ts_code",
    "name",
    "exchange",
    "market",
    "list_status",
    "list_date",
    "act_name",
    "act_ent_type",
  ]);
  const company = trans1(stock_companyOrigin, [
    "ts_code",
    "website",
    "employees",
    "main_business",
  ]);

  industry = industry.map((item: Record<string, any>) => {
    const ts_code = item.ts_code;
    const basicItem =
      basic.find((v: Record<string, any>) => {
        return v.ts_code === ts_code;
      }) || {};
    const companyItem =
      company.find((v: Record<string, any>) => {
        return v.ts_code === ts_code;
      }) || {};

    const financeItem = financeData[ts_code]?.find((value: financeItem) => {
      return value.end_date === useDate;
    });

    return {
      roic: financeItem?.roic,
      ...item,
      ...basicItem,
      ...companyItem,
    };
  });
  industry = industry.filter((item: any) => item.name);
  return industry;
};

const initDta = async () => {
  const financeData = await getFinance();
  const list: Record<string, any>[] = await mergeData(financeData);

  window.industry = list;
  return list;
};

export default initDta;
