import styles from "./index.module.css";

const PageHome = (props: any) => {
  const handleGoto = (pageName: string) => {
    props.setRouter(pageName);
  };
  return (
    <div className={styles.home}>
      <div className={styles.card} onClick={() => handleGoto("self")}>
        SELF
      </div>
      <div className={styles.card} onClick={() => handleGoto("industry")}>
        INDUSTRY
      </div>
      <div className={styles.card} onClick={() => handleGoto("manage")}>
        DATA
      </div>
    </div>
  );
};

export default PageHome;
