import { CommonApiResponse } from "../Components/PageManage";

export const trans1 = (
  d: CommonApiResponse,
  keys: string[]
): Record<string, any>[] => {
  const { fields, items } = d || {};
  const arr: Record<string, any>[] = [];

  items?.forEach((item: any[][]) => {
    const obj: Record<string, any> = {};
    item.forEach((value: any, index: number) => {
      const key: string = fields[index];
      let isNeed = false;
      if (keys && keys.length) {
        if (value !== null && keys.includes(key)) {
          isNeed = true;
        }
      } else {
        if (value !== null) {
          isNeed = true;
        }
      }
      if (isNeed) {
        obj[key] = value;
      }
    });
    arr.push(obj);
  });

  return arr;
};
