import { financeLocalforage } from "../utils/localforage";
import { trans1 } from "../utils/trans";
import { CommonApiResponse } from "../Components/PageManage";

export type financeItem = {
  end_date: string;
  roic: number;
  eps: number; // 
  basic_eps_yoy: number; // (%)
  roe: number;
  grossprofit_margin: number; // 
  netprofit_margin: number; // 
  ar_turn: number; // 
  inv_turn: number; // 
  assets_turn: number; // 
  debt_to_assets: number; // 
  current_ratio: number; // 
  quick_ratio: number; // 
};

const initFinance = async () => {
  const data: Record<string, any[]> = {};
  await financeLocalforage.iterate((value: CommonApiResponse, key: string) => {
    const item = trans1(value, [
      "end_date",
      "roic",
      "eps",
      "basic_eps_yoy",
      "roe",
      "grossprofit_margin",
      "netprofit_margin",
      "ar_turn",
      "inv_turn",
      "assets_turn",
      "debt_to_assets",
      "current_ratio",
      "quick_ratio",
    ]);
    item?.sort((a: any, b: any) => {
      return Number(a.end_date) - Number(b.end_date);
    });
    data[key] = item;
  });
  return data;
};

const getFinance = async (): Promise<Record<string, financeItem[]>> => {
  if (!window.financeData) {
    window.financeData = await initFinance();
  }
  return window.financeData;
};

export default getFinance;
