import localforage from "localforage";

const financeLocalforage = localforage.createInstance({
  name: "finance",
});

const kLocalforage = localforage.createInstance({
  name: "k",
});

if (!window.financeLocalforage) {
  window.financeLocalforage = financeLocalforage;
}
if (!window.kLocalforage) {
  window.kLocalforage = kLocalforage;
}

export { financeLocalforage, kLocalforage };
