import dayjs from "dayjs";
import { useEffect, useState } from "react";

const selfGistId = "b7235e7e7b5012ef888658c444f6956d";
const gistToken = ["ghp_ykONB0ud", "9rdnhPhS8ccga", "1PTPbdyqU4Xcb2E"].join("");

export type selfCodeItem = {
  ts_code: string;
  name: string;
  isBuy?: boolean;
  tag?: string | number;
};

const getGist = async (
  gistId: string
): Promise<Record<string, selfCodeItem>> => {
  const response = await fetch(`https://api.github.com/gists/${gistId}`, {
    headers: {
      Authorization: `token ${gistToken}`,
    },
  });
  if (!response.ok) {
    throw new Error(`getGist error! status: ${response.status}`);
  }
  const res = await response.json();
  let content = res.files["self.json"].content;
  content = JSON.parse(content);
  return content;
};

async function updateGist(gistId: string, description = "", files: any) {
  if (!localStorage.getItem("canUpdateSelf")) {
    throw new Error(
      "can't update self! please setting localStorage canUpdateSelf!"
    );
  }
  const response = await fetch(`https://api.github.com/gists/${gistId}`, {
    method: "PATCH",
    headers: {
      Authorization: `token ${gistToken}`,
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      description,
      files,
    }),
  });
  if (!response.ok) {
    throw new Error(`updateGist error! status: ${response.status}`);
  }
  return await response.json();
}

const useSelfCode = () => {
  const [self, setSelf] = useState<Record<string, selfCodeItem>>({});

  const init = async () => {
    const data = await getGist(selfGistId);
    setSelf(data);
  };

  useEffect(() => {
    init();
  }, []);

  return {
    self,
    setSelf: async (item: selfCodeItem) => {
      const { isBuy, ts_code, tag } = item;
      if (!isBuy && !tag) {
        delete self[ts_code];
      } else {
        self[ts_code] = item;
      }

      const res = await updateGist(
        selfGistId,
        dayjs().format("YYYYMMDD HH:mm:ss"),
        {
          "self.json": {
            content: JSON.stringify(self),
          },
        }
      );
      let content = res.files["self.json"].content;
      content = JSON.parse(content);
      setSelf(content);
    },
  };
};

export default useSelfCode;
