import dayjs from "dayjs";
import { financeItem } from "../../Data/getFinance";
import { geenrateSubtext1 } from "../../utils/generateEChartOption";
import EChartReact from "../EChartReact";

const Margin = (props: any) => {
  const { finance, ts_code } = props;
  const getAssetsOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("assets_turn", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.assets_turn),
          type: "bar",
          animation: false,
          showBackground: true,
        },
      ],
    };
    return option;
  };

  const getArOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("ar_turn", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.ar_turn),
          type: "bar",
          animation: false,
          showBackground: true,
        },
      ],
    };
    return option;
  };

  const getInvOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("inv_turn", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.inv_turn),
          type: "bar",
          showBackground: true,
          animation: false,
        },
      ],
    };
    return option;
  };

  const assetsOption = getAssetsOption();
  const arOption = getArOption();
  const invOption = getInvOption();

  return (
    <div style={{ display: "flex", width: "100%" }}>
      <EChartReact option={assetsOption} />
      <EChartReact option={arOption} />
      <EChartReact option={invOption} />
    </div>
  );
};
export default Margin;
