import dayjs from "dayjs";
import { kLocalforage } from "./localforage";

const exMap: any = {
  SZ: "0",
  SH: "1",
};

const kltMap: any = {
  day: 101,
  week: 102,
  month: 103,
};

export type KLineType = {
  day: KItemType[];
  week: KItemType[];
  month: KItemType[];
  quarter: KItemType[];
  year: KItemType[];
};

export type KItemType = {
  start_date: string; // 
  date: string; // 
  open: number;
  close: number;
  high: number;
  low: number;
  volume: number;
  ma6?: number;
};

const getCache = async (ts_code: string) => {
  const day = dayjs().day();
  const hour = dayjs().hour();

  if ([1, 2, 3, 4, 5].includes(day) && [9, 10, 11, 13, 14].includes(hour)) {
    return null;
  }

  const d: any = await kLocalforage.getItem(ts_code);
  if (d) {
    return d;
  }
  return null;
};

const strArrToObjArr = (data: string[]) => {
  return data?.map((item: string) => {
    const [date, open, close, high, low, volume] = item?.split(",") || [];
    return {
      date: date,
      open: Number(open),
      close: Number(close),
      high: Number(high),
      low: Number(low),
      volume: Number(volume),
    };
  });
};

const calcMa = (list: KItemType[]) => {
  list.forEach((item: any, index: number) => {
    if (index >= 5) {
      let total = 0;
      list.slice(index - 5, index + 1).forEach((item: any) => {
        total += item.close;
      });
      let avg = total / 6;
      item.avg = Number(avg.toFixed(2));
    }
  });
};

const toDay = (data: KItemType[]) => {
  calcMa(data);
  return data;
};
const toWeek = (data: KItemType[]) => {
  let weeklyData: KItemType[] = [];
  let currentWeek: KItemType | null = null;
  let currentWeekNo: number = -1;

  data.forEach((day: KItemType) => {
    const weekOfYear = dayjs(day.date).week(); // 

    if (currentWeekNo !== weekOfYear) {
      // 
      if (currentWeek) {
        weeklyData.push(currentWeek);
      }

      currentWeekNo = weekOfYear;
      // 
      currentWeek = {
        open: day.open,
        close: day.close,
        high: day.high,
        low: day.low,
        volume: day.volume,
        start_date: day.date, // 
        date: day.date,
      };
    } else if (currentWeek) {
      // 
      currentWeek.high = Math.max(currentWeek.high, day.high);
      currentWeek.low = Math.min(currentWeek.low, day.low);
      currentWeek.close = day.close;
      currentWeek.volume += day.volume;
      currentWeek.date = day.date; // 
    }

    // 
    if (data.indexOf(day) === data.length - 1 && currentWeek) {
      weeklyData.push(currentWeek);
    }
  });

  calcMa(weeklyData);
  return weeklyData;
};
const toMonth = (data: KItemType[]) => {
  let monthData: KItemType[] = [];
  let currentMonthData: KItemType | null = null;
  let currentMonthNo: number = -1;

  data.forEach((day: KItemType) => {
    const monthOfYear = dayjs(day.date).month(); // 

    if (currentMonthNo !== monthOfYear) {
      // 
      if (currentMonthData) {
        monthData.push(currentMonthData);
      }

      currentMonthNo = monthOfYear;
      // 
      currentMonthData = {
        open: day.open,
        close: day.close,
        high: day.high,
        low: day.low,
        volume: day.volume,
        start_date: day.date, // 
        date: day.date,
      };
    } else if (currentMonthData) {
      // 
      currentMonthData.high = Math.max(currentMonthData.high, day.high);
      currentMonthData.low = Math.min(currentMonthData.low, day.low);
      currentMonthData.close = day.close;
      currentMonthData.volume += day.volume;
      currentMonthData.date = day.date; // 
    }

    // 
    if (data.indexOf(day) === data.length - 1 && currentMonthData) {
      monthData.push(currentMonthData);
    }
  });

  calcMa(monthData);
  return monthData;
};
const toQuarter = (data: KItemType[]) => {
  // dataK
  const quarters = [];
  let currentQuarterData: any = null;
  let currentQuarter: any = null;

  data.forEach((day) => {
    const date = new Date(day.date);
    const year = date.getFullYear();
    const month = date.getMonth(); // 0 - 11
    const quarter = Math.floor(month / 3);

    // 
    if (year !== currentQuarter?.year || quarter !== currentQuarter?.quarter) {
      if (currentQuarterData) {
        // 
        quarters.push(currentQuarterData);
      }
      currentQuarter = { year, quarter };
      currentQuarterData = {
        open: day.open,
        high: day.high,
        low: day.low,
        close: day.close,
        volume: day.volume,
        start_date: day.date, // 
        date: day.date,
      };
    } else {
      // 
      currentQuarterData.high = Math.max(currentQuarterData.high, day.high);
      currentQuarterData.low = Math.min(currentQuarterData.low, day.low);
      currentQuarterData.close = day.close;
      currentQuarterData.volume += day.volume;
      currentQuarterData.date = day.date; // 
    }
  });

  // 
  if (currentQuarterData) {
    quarters.push(currentQuarterData);
  }

  calcMa(quarters);

  return quarters;
};

const toYear = (data: KItemType[]) => {
  let yearData: KItemType[] = [];
  let currentYearData: KItemType | null = null;
  let currentYearNo: number = -1;

  data.forEach((day: KItemType) => {
    const monthOfYear = dayjs(day.date).year(); // 

    if (currentYearNo !== monthOfYear) {
      // 
      if (currentYearData) {
        yearData.push(currentYearData);
      }

      currentYearNo = monthOfYear;
      // 
      currentYearData = {
        open: day.open,
        close: day.close,
        high: day.high,
        low: day.low,
        volume: day.volume,
        start_date: day.date, // 
        date: day.date,
      };
    } else if (currentYearData) {
      // 
      currentYearData.high = Math.max(currentYearData.high, day.high);
      currentYearData.low = Math.min(currentYearData.low, day.low);
      currentYearData.close = day.close;
      currentYearData.volume += day.volume;
      currentYearData.date = day.date; // 
    }

    // 
    if (data.indexOf(day) === data.length - 1 && currentYearData) {
      yearData.push(currentYearData);
    }
  });

  calcMa(yearData);
  return yearData;
};

const getKLine = async (ts_code: string): Promise<KLineType> => {
  const [code, ex] = ts_code.split(".");

  let cache = await getCache(ts_code);
  if (cache) {
    cache = strArrToObjArr(cache) || [];
    return {
      day: toDay(cache),
      week: toWeek(cache),
      month: toMonth(cache),
      quarter: toQuarter(cache),
      year: toYear(cache),
    };
  }

  try {
    const secid = `${exMap[ex]}.${code}`; // A 0.xxxxxx, A 1.xxxxxx
    const fields1 = `f1,f3`; // data { code name }
    // data.klines [[     ], []]
    const fields2 = "f51,f52,f53,f54,f55,f56";
    const klt = kltMap.day;
    const fqt = "1"; //  - 1 
    const beg = "19900101";
    const end = "20500101";
    let url = `https://push2his.eastmoney.com/api/qt/stock/kline/get?&secid=${secid}&fields1=${fields1}&fields2=${fields2}&klt=${klt}&fqt=${fqt}&beg=${beg}&end=${end}`;
    let response = await fetch(url);
    let data = await response.json();
    data = data?.data?.klines as string[];
    if (data) {
      kLocalforage.setItem(ts_code, data);
    }

    data = strArrToObjArr(data) || [];
    return {
      day: toDay(data),
      week: toWeek(data),
      month: toMonth(data),
      quarter: toQuarter(data),
      year: toYear(data),
    };
  } catch (error: any) {
    console.error(error);
    return {
      day: [],
      week: [],
      month: [],
      quarter: [],
      year: [],
    };
  }
};

export default getKLine;
