import { useEffect, useRef } from "react";
import { tooltipPosition } from "../../utils/generateEChartOption";

const EChartReact = (props: any) => {
  const { width = "50%", height = "45vh", option } = props;
  const chartRef = useRef<any>(null);

  const init = () => {
    if (chartRef?.current && option) {
      if (option.tooltip) {
        option.tooltip.position = tooltipPosition;
      }

      const myChart = window.echarts.init(chartRef?.current);
      myChart.setOption(option);
    }
  };

  useEffect(() => {
    init();
  }, [option]);

  if (!option) {
    return null;
  }

  return <div style={{ width, height }} ref={chartRef}></div>;
};

export default EChartReact;
