import { message } from "antd";
import dayjs from "dayjs";
import { useEffect, useState } from "react";

import styles from "./index.module.css";
import getKLine from "../../utils/getKLine";
import EChartReact from "../EChartReact";

const titleMap: any = {
  day: "K(100)",
  month: "K",
  quarter: "K",
};

const ItemK = (props: any) => {
  const { ts_code } = props;
  const [dayOption, setDayOption] = useState<any>(null);
  const [monthOption, setMonthOption] = useState<any>(null);
  const [quarterOption, setQuarterOption] = useState<any>(null);

  const getEChartOption = (period: string, klineData: any[]) => {
    const option = {
      title: {
        text: titleMap[period],
      },
      xAxis: {
        data: klineData.map((item: any) => {
          if (period === "day") {
            return dayjs(item.date).format("MMDD");
          } else if (period === "month") {
            return dayjs(item.date).format("YYYYMM");
          } else if (period === "quarter") {
            return dayjs(item.date).year();
          }
        }),
      },
      yAxis: {
        min: "dataMin",
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          animation: false,
          type: "cross",
        },
        formatter: function (params: any[]) {
          const { name, data } = params?.[0] || {};
          const [index, open, close, low, high] = data;
          const ma6Value = params?.[1]?.data?.value;
          const preClose = klineData[index - 1]?.close || open;

          let tempValue1 = "";
          let str = name + "<br/>";

          tempValue1 = (((close - preClose) / preClose) * 100).toFixed(2);
          str += `: ${tempValue1}<br/>`;

          let tempValue2 = (((high - low) / high) * 100).toFixed(2);
          let tempValue3 = (((high - low) / low) * 100).toFixed(2);
          str += `: -${tempValue2}, +${tempValue3}<br/>`;

          let tempValue4 = "";
          if (close >= ma6Value) {
            tempValue4 = `-` + (((close - ma6Value) / close) * 100).toFixed(2);
          } else {
            tempValue4 = `+` + (((ma6Value - close) / close) * 100).toFixed(2);
          }
          str += `: ${tempValue4}<br/>`;

          return str;
        },
      },
      series: [
        {
          type: "candlestick",
          animation: false,
          data: klineData.map((item: any) => [
            item.open,
            item.close,
            item.low,
            item.high,
          ]),
        },
        {
          name: "MA6",
          type: "line",
          smooth: true,
          showSymbol: false,
          animation: false,
          lineStyle: {
            width: 1,
            color: "#1677ff",
          },
          data: klineData.map((item: any) => ({
            value: item.avg,
            symbol: "none",
          })),
        },
      ],
    };

    return option;
  };

  const init = async () => {
    if (ts_code) {
      const res = await getKLine(ts_code);
      const dayLength = res?.day?.length;
      const monthLength = res?.month?.length;
      if (dayLength) {
        setDayOption(getEChartOption("day", res?.day?.slice(dayLength - 150)));
        setMonthOption(
          getEChartOption("month", res?.month?.slice(monthLength - 100))
        );
        setQuarterOption(getEChartOption("quarter", res?.quarter));
      } else {
        message.info(`${ts_code} K`);
      }
    }
  };

  useEffect(() => {
    init();
  }, [ts_code]);

  return (
    <>
      <div className={styles.container}>
        <EChartReact option={quarterOption} />
        <EChartReact option={monthOption} />
      </div>
      <EChartReact width="100%" option={dayOption} />
    </>
  );
};

export default ItemK;
