import dayjs from "dayjs";
import { financeItem } from "../../Data/getFinance";
import { geenrateSubtext1 } from "../../utils/generateEChartOption";
import EChartReact from "../EChartReact";

const Profit = (props: any) => {
  const { finance, ts_code } = props;

  const getRoeOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "ROE",
        subtext: geenrateSubtext1("roe", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.roe),
          type: "bar",
          animation: false,
          showBackground: true,
        },
        {
          data: finance?.map(() => ({ value: 15, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const getNetprofitOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("netprofit_margin", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.netprofit_margin),
          type: "bar",
          showBackground: true,
          animation: false,
        },
        {
          data: finance?.map(() => ({ value: 10, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const getGrossprofitOption = () => {
    if (!finance) return null;
    const option = {
      title: {
        text: "",
        subtext: geenrateSubtext1("grossprofit_margin", ts_code),
      },
      tooltip: { trigger: "axis" },
      xAxis: {
        type: "category",
        data: finance?.map((item: financeItem) => dayjs(item.end_date).year()),
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: finance?.map((item: financeItem) => item.grossprofit_margin),
          type: "bar",
          animation: false,
          showBackground: true,
        },
        {
          data: finance?.map(() => ({ value: 25, symbol: "none" })),
          type: "line",
          animation: false,
        },
      ],
    };
    return option;
  };

  const roeOption = getRoeOption();
  const grossprofitOption = getGrossprofitOption();
  const netprofitOption = getNetprofitOption();

  return (
    <div style={{ display: "flex", width: "100%" }}>
      <EChartReact option={roeOption} />
      <EChartReact option={grossprofitOption} />
      <EChartReact option={netprofitOption} />
    </div>
  );
};
export default Profit;
